@import url('./stylesConstants.css');

.Header {
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  height: 75px;
  background-color: var(--white-color);
  align-items: center;
  position: fixed;
  border-bottom: 1px solid var(--dark-green-color);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  z-index: 2;
}

.Name {
  color: var(--dark-green-color);
  font-family: 'Noto Sans JP', sans-serif;
  margin-left: 23px;
  font-weight: bold;
  font-size: 34px;
  line-height: 49px;
}

.PopUpName {
  margin-left: 0px;
}

.ButtonContainer {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}

.BackButton {
  height: 75px;
  padding-left: 15px;
  padding-right: 18px;
}

.PopUpHeader {
  display: flex;
  align-items: center;
}

.NotPetButton {
  display: flex;
  align-items: center;
  height: 75px;
  outline: none;
  border: none;
  background-color: #fff;
  cursor: pointer;
}

@media (max-width: 950px) {
  .Header {
    height: 65px;
  }

  .Name {
    font-size: 25px;
    margin-left: 15px;
  }

  .NotPetButton {
    height: 65px;
  }
}

@media (max-width: 490px) {
  .Header {
    height: 50px;
  }

  .Name {
    font-size: 18px;
    margin-left: 10px;
    line-height: 20px;
  }

  .NotPetButton {
    height: 50px;
  }
}
