@import url('./stylesConstants.css');

.Content {
  margin-top: 100px;
  width: 100%;
}

.Container {
  display: flex;
  justify-content: center;
  margin-top: 25px;
  height: 609px;
  margin-bottom: 50px;
}

.ContainerButton {
  display: flex;
  justify-content: center;
}

.NotPet {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 70vh;
}

.NotPetText {
  font-family: 'Noto Sans JP', sans-serif;
  font-size: 34px;
  line-height: 49px;
  margin-bottom: 29px;
}

a {
  text-decoration: none;
}

a:visited {
  color: white;
}

.Button {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--dark-green-color);
  border-radius: 10px;
  font-family: 'Noto Sans JP', sans-serif;
  font-weight: 500;
  color: white;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.NotPetButton {
  font-size: 34px;
  line-height: 49px;
  width: 401px;
  height: 68px;
}

.NewPetButton {
  font-size: 24px;
  line-height: 35px;
  width: 1318px;
  height: 50px;
}

.Plus {
  margin-right: 10px;
  margin-top: 4px;
  height: 35px;
}

@media (max-width: 1400px) {
  .Container {
    height: 500px;
  }

  .NewPetButton {
    width: 1080px;
  }
}

@media (max-width: 1200px) {
  .NewPetButton {
    width: 828px;
  }
}

@media (max-width: 950px) {
  .Container {
    height: 400px;
    margin-top: 15px;
  }

  .NewPetButton {
    width: 614px;
    height: 35px;
    font-size: 18px;
  }
}

@media (max-width: 660px) {
  .Content {
    margin-top: 80px;
  }

  .NewPetButton {
    width: 451px;
  }

  .Container {
    flex-direction: column;
    height: auto;
    margin-left: 15px;
    align-items: center;
  }
}

@media (max-width: 490px) {
  .Content {
    margin-top: 65px;
  }

  .NewPetButton {
    width: 300px;
    height: 30px;
    font-size: 15px;
  }
}
