@import url('./stylesConstants.css');

.Button {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Noto Sans JP', sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 26px;
  width: 176px;
  height: 75px;
  border: none;
  outline: none;
  cursor: pointer;
}

.Enabled {
  color: white !important;
  text-decoration: none;
  background-color: var(--dark-green-color);
}

.Disabled {
  color: black !important;
  text-decoration: none;
  background-color: var(--white-color);
}

.optionsBox {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 78px;
  right: 3px;
  background-color: white;
  border: 1px solid var(--dark-green-color);
  border-radius: 5px;
  width: 160px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  overflow: hidden;
}

.option {
  display: flex;
  flex-direction: row;
  align-items: center;
  border-top: 1px solid var(--dark-green-color);
  border-left: none;
  border-right: none;
  border-bottom: none;
  height: 42px;
  color: black !important;
  padding-left: 10px;
  font-family: 'Noto Sans JP', sans-serif;
  outline: none;
  text-decoration: none;
  background-color: white;
  font-weight: 300;
  font-size: 14px;
  line-height: 26px;
  cursor: pointer;
}
.option:hover {
  background-color: var(--dark-green-color);
  color: #fff !important;
  font-weight: 500;
}
.option:first-child {
  border: none;
}

.optionText {
  margin: 0px;
}

@media (max-width: 950px) {
  .Button {
    width: 130px;
    height: 65px;
  }
}

@media (max-width: 490px) {
  .Button {
    width: 80px;
    font-size: 13px;
    height: 50px;
    text-align: center;
    line-height: 17px;
  }

  .optionsBox {
    top: 60px;
    right: 3px;
    width: 100px;
    height: 60px;
  }

  .option,
  .optionText {
    font-size: 12px;
  }
}
